@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700,800,900&display=swap);
.presentation-card-container a, a:link, a:visited, a:hover{
  color: #13161a !important;
  font-size: 20px;
}

.perfil{
  width: 100%;
  height: 100%;
  max-height: 500px;
  max-width: 500px;
  border-radius: 50%;
  margin: 25px 0;
}

.skill-list{
  list-style-type: none;
  margin: 0;
  padding: 15px;
  width: auto;
  max-width: 700px;
  height: auto;
}

.skill{
  float: left;
  margin: 10px; 
}

.presentation-card-container .skill-icon, .map-icon{
  height: 50px;
  width: 50px;
}

.presentation-card-container{
  display: flex;
  max-width: 1500px;
  width: auto;
  height: auto;
  margin-left: 10%;
  margin-right: 10%;
  padding-top: 50px;
  padding-bottom: 50px;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}

.presentation-card-container h1{
  font-size: 70px;
}

.presentation-card-container h2{
  font-size: 40px;
  font-weight: normal;
  padding: 15px;
}
.presentation-card-container p{
  font-size: 30px;
  font-weight: normal;
  color: #13161a;
  text-align: center;
}

/* Responsividade */

@media only screen and (max-width:767px) {
  .presentation-card-container a, 
  .presentation-card-container a:link, 
  .presentation-card-container a:visited, 
  .presentation-card-container a:hover{
    color: #13161a !important;
    font-size: 12px;
  }

  .skill{
    font-size: 8px;
    margin: 2px;
  }

  .presentation-card-container h1{
    font-size: 35px;
    padding: 5px;
  }

  .presentation-card-container h2{
    font-size: 25px;
    padding: 5px;
  }

  .presentation-card-container p{
    font-size: 15px
  }

  .presentation-card-container .skill-icon, .map-icon{
    height: 30px;
    width: 30px;
  } 

  .perfil{
    border-radius: 50%;
    width: 170px;
    height: 170px;
  }
}

*{
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

body{
  font: bold 14px Roboto, sans-serif !important;
  color: #13161a;
  background: #f2f2f2 !important;
}

p, a, h1, h2, h3, h4{
  color: #13161a;
}


.barra-superior{
  display: flex;
  align-items: center;
  background-color: #13161a !important;
}

  .barra-superior h1{
    font: bold 50px Roboto Slab, sans-serif;
    color: #f2f2f2;
    padding: 0;
    margin: 0;
  }
  .barra-superior h2{
    font-size: 25px;
    color: #f2f2f2;
    transition: opacity 0.2s;
  }
  .barra-superior h2:hover{
    opacity: 0.7;
  }

.menu{
  display: flex;
  justify-content: flex-end;
}

@media only screen and (max-width:767px){
  .barra-superior h1{
    justify-self: center;
    font-size: 40px;
  }
  .barra-superior h2{
    font-size: 20px;
  }
}
.social-icon{
  height: 50px;
  width: 50px;
  margin: 10px;
}

/* Responsividade */

@media only screen and (max-width:767px) {

  .social-icon{
    height: 35px;
    width: 35px;
    margin: 5px;
  } 
}

.sobre-mim-container{
  display: flex;
  max-width: 1500px;
  width: auto;
  height: auto;
  margin-left: 10%;
  margin-right: 10%;
  padding-top: 50px;
  padding-bottom: 50px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.sobre-mim-container a, a:link, a:visited, a:hover{
  color: #13161a !important;
  font-size: 25px;
}

.sobre-mim-container h1{
  font-size: 70px;
}

.sobre-mim-container h2{
  font-size: 40px;
  font-weight: normal;
  padding: 15px;
}

.sobre-mim-container h3{
  font-size: 50px;
  font-weight: bold;
  text-align: left;
  margin-top: 30px;
}

.sobre-mim-container p{
  font-size: 30px;
  font-weight: normal;
  color: #13161a;
  text-align: left;
}

.sobre-mim-container .perfil{
  width: 100%;
  height: 100%;
  max-height: 500px;
  max-width: 500px;
  border-radius: 50%;
  margin: 25px 0;
}

.sobre-mim-container .map-icon{
  height: 50px;
  width: 50px;
}


/* Responsividade */

@media only screen and (max-width:767px) {
  .sobre-mim-container a, 
  .sobre-mim-container a:link, 
  .sobre-mim-container a:visited, 
  .sobre-mim-container a:hover{
    color: #13161a !important;
    font-size: 15px;
  }
  
  .sobre-mim-container h1{
    font-size: 35px;
    padding: 5px;
  }

  .sobre-mim-container h2{
    font-size: 25px;
    padding: 5px;
  }

  .sobre-mim-container h3{
    margin-top: 10px;
    font-size: 25px;
  }

  .sobre-mim-container p{
    font-size: 15px
  }

  .sobre-mim-container .skill-icon,.sobre-mim-container .map-icon{
    height: 30px;
    width: 30px;
  } 

  .sobre-mim-container .perfil{
    border-radius: 50%;
    width: 170px;
    height: 170px;
  }
}

.post-card{
  display: flex;
  width: auto;
  height: auto;
  background-color: #e2e2e2;
  margin: 2%;
  border-radius: 20px;
  padding: 0;
}

.icon-container{
  display: flex;
  padding-right: 5%;
  padding-left: 20px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 15px;
}

.descr-container{
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 18px;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 10px;
}

.icon{
  width: 50px;
  height: 50px;
}

.descr-container h1{
  font-size: 45px;
}

@media only screen and (max-width:767px){
  
  .descr-container h1{
    font-size: 30px;
  }

  .descr-container{
    font-size: 12px;
  }

  .icon{
    width: 40px;
    height: 40px;
  }
}
.content-container{
  padding: 5%;
  /* margin-left: 10%;
  margin-right: 10%;*/
  display: flex;
  flex-direction: column;
}

.content-container h1{
  font-size: 55px;
  font-weight: bold;
  padding-top: 15px;
}

.content-container h2{
  font-size: 40px;
  font-weight: bold;
  padding-top: 30px;
}

.content-container h3, .content-container h3 a{
  font-size: 35px;
  font-weight: bold;
  padding-top: 20px;
}

.content-container h4{
  font-size: 30px;
  font-weight: bold;
  padding-top: 15px;
}

.content-container p, .content-container p a{
  text-indent: 2em;
  font-size: 26px;
  font-weight: normal;
  text-align: justify;
}
.content-container p a{
  font-weight: 500;
}

.content-container a{
  font-size: 25px;
  font-weight: 500;
}

.content-container img{
  align-self: center;
  padding: 20px;
  max-width: 1050px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.content-container .tabela{
  max-width: 600px;
}

.content-container .code-container{
  background: #13161a;
  max-width: 1000px;
  width: auto;
  height: auto;
  padding: 50px;
  border-radius: 20px;
  text-align: left;
  align-self: left;
}
.content-container code{
  font-size: 18px;
  color: #f2f2f2;  
  background-color: #13161a;
  border-radius: .2rem;
  line-height: 30px;
}
.content-container .equacao{
  text-indent: 0;
  text-align: center;
  padding: 20px;
}

@media only screen and (max-width:767px){
  .content-container .code-container{
    height: auto;
    padding: 25px;
    border-radius: 10px;
    text-align: left;
    align-self: left;
  }

  .content-container{
    padding: 2.5%;
  }

  .content-container .equacao{
    padding: 10px;
  }

  .content-container a{
    font-size: 14px;
    font-weight: 500;
  }

  .content-container code{
    font-size: 14px;
    color: #f2f2f2;
    background-color: #13161a;
    border-radius: .2rem;
    line-height: 22px;
  }

  .content-container img{
    width: 80%;
    max-width: 350px;
    padding: 10px;
  }

  .content-container h1{
    font-size: 40px;
    font-weight: bold;
    padding-top: 7px;
    padding-bottom: 7px;
  }
  
  .content-container h2{
    font-size: 30px;
    font-weight: bold;
    padding-top: 15px;
  }

  .content-container h3, .content-container h3 a{
    font-size: 25px;
    font-weight: bold;
    padding-top: 10px;
  }

  .content-container h4{
    font-size: 20px;
    padding-top: 5px;
  }
  
  .content-container p, .content-container p a{
    text-indent: 2em;
    font-size: 18px;
    font-weight: normal;
  }
  .content-container p a{
    font-weight: 500;
  }
}
